    }
  }
  /*============MEDIA QUERIES (SMALL DEVICES)==================*/
  @media screen and (max-width:600px) {
    .services_container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
   
  }
